import React from 'react';
import logo from './logo.svg';
import './App.css';
import { Analyzer } from 'fuzzy-searcher';

function App() {
  const anlyzer = new Analyzer();
  const list = ["KE", "KEO", "KEA", "KEYS", "AKEY", "BLA", "TRUCO", "NOT", "TEST"];
  anlyzer.load(list);
  const matches = anlyzer.search("KEY", 1);
  return (
    <div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <p>
          {matches.join(" ")}
        </p>
        <a
          className="App-link"
          href="https://reactjs.org"
          target="_blank"
          rel="noopener noreferrer"
        >
          Learn React
        </a>
      </header>
    </div>
  );
}

export default App;
